html {
  -ms-overflow-style: -ms-autohiding-scrollbar;
  text-size-adjust: 100%;
}

main {
  display: block;
}

details {
  display: block;
}

pre {
  overflow: auto;
}

progress {
  display: inline-block;
}

summary {
  display: block;
}

template {
  display: none;
}

*,
::before,
::after {
  border-style: solid;
  border-width: 0;
  box-sizing: border-box;
}

* {
  font-size: inherit;
  line-height: inherit;
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

audio :not([controls]) {
  display: none;
}

[aria-busy=true] {
  cursor: progress;
}

[aria-controls] {
  cursor: pointer;
}

[aria-disabled] {
  cursor: default;
}

[hidden] {
  display: none;
}

button {
  overflow: visible;
}

input {
  border-radius: 0;
  line-height: normal;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: button;
  cursor: pointer;
}
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border: 0 none !important;
  padding: 0 !important;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number] {
  width: auto;
}

[type=search] {
  appearance: textfield;
}

button,
input,
select,
textarea {
  background-color: transparent;
  color: inherit;
  font-family: inherit;
  font-style: inherit;
  font-weight: inherit;
}

textarea {
  overflow: auto;
  resize: vertical;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

audio,
canvas,
iframe,
img,
svg,
video {
  max-width: 100%;
  vertical-align: middle;
}

a {
  background-color: transparent;
}
a:active, a:hover {
  outline: 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: bold;
}

code,
kbd,
pre,
samp {
  font-family: monospace;
  font-size: inherit;
}

dfn {
  font-style: italic;
}

nav ol,
nav ul {
  list-style: none;
}

small {
  font-size: 0.75em;
}

sub,
sup {
  font-size: 0.75em;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

[data-theme=light] {
  --color-background: #efefef;
  --color-text: #0d2128;
  --color-text-light: #326c7f;
  --color-border: #7f7f7f;
  --color-border-light: #939393;
  --color-action: #b10e5f;
  --color-action-hover: #89074c;
}
[data-theme=light] [data-theme=invert] {
  --color-background: #0d2128;
  --color-text: #efefef;
  --color-text-light: #94b0b9;
  --color-border: #7f7f7f;
  --color-border-light: #6c6c6c;
  --color-action: #d99fbc;
  --color-action-hover: #e1bccf;
}

[data-theme=dark] {
  --color-background: #0d2128;
  --color-text: #efefef;
  --color-text-light: #94b0b9;
  --color-border: #7f7f7f;
  --color-border-light: #6c6c6c;
  --color-action: #d99fbc;
  --color-action-hover: #e1bccf;
}
[data-theme=dark] [data-theme=invert] {
  --color-background: #efefef;
  --color-text: #0d2128;
  --color-text-light: #326c7f;
  --color-border: #7f7f7f;
  --color-border-light: #939393;
  --color-action: #b10e5f;
  --color-action-hover: #89074c;
}

[data-theme] {
  background-color: var(--color-background, #efefef);
  color: var(--color-text, #0d2128);
}

@font-face {
  font-family: "system";
  font-style: normal;
  font-weight: 300;
  src: local(".SFNSText-Light"), local(".HelveticaNeueDeskInterface-Light"), local(".LucidaGrandeUI"), local("Ubuntu Light"), local("Segoe UI Light"), local("Roboto-Light"), local("DroidSans"), local("Tahoma");
}
:root {
  font-family: system, system-ui, sans-serif;
  font-size: calc(16px + 0.25vw);
  line-height: 1.6;
}

h2 {
  font-size: calc(1.2649110641rem + 0.75vw);
  line-height: 1.2;
}

h3 {
  font-size: 1.2649110641rem;
}

p {
  margin-bottom: 1.6rem;
}

blockquote {
  margin-bottom: 1.6rem;
}
blockquote p:last-of-type {
  margin-bottom: 0.4rem;
}

a:link, a:visited {
  color: var(--color-action, #b10e5f);
}
a:hover, a:focus, a:active {
  color: var(--color-action-hover, #89074c);
}

.is-hidden {
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(1px 1px 1px 1px);
  height: 1px;
  overflow: hidden;
  pointer-events: none;
  position: absolute;
  width: 1px;
}

[data-layout=grid] {
  display: grid;
  grid-template-rows: auto 1fr auto;
  margin: 0 auto;
  max-width: 42rem;
  min-height: 100vh;
  padding: 1.6rem 3.2rem;
}

[data-layout=header] {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 1.6rem;
}